import React from 'react';
import { useLocation,useNavigate } from 'react-router-dom';
import styles from '../styles/ReportList.module.css';
import {Button, Row,Col } from 'react-bootstrap';
import "../styles/Home.css";
import { submitGetHistoricalReport } from '../api';

const ReportList = () => {
    const location = useLocation();
    // const reports = location.state?.reports?.list_report || [];
    const reports = location.state?.reports || [];

    const truncateReport = (report) => {
        const lines = report.split('\n');
        return lines.slice(0, 10).join('\n');
    };

    const formatTitle = (title) => {
        return title
            .split('_')
            .map(word => word.charAt(0).toUpperCase() + word.slice(1))
            .join(' ');
    };
    // const report view
    const navigate = useNavigate();
    const handleRedirect = (title,type_report, timestamp, report) => {
        navigate("/get_historical_report", { state: { title, type_report, timestamp, report } });
    };

    const handleGoBack = () => {
        navigate(-1);
    };
    const handleGetReport = async (token) =>{
        try {
            
            const data = await submitGetHistoricalReport(token);
            
            if (data.error) {
                console.log(data.error)
            } else {
              
                handleRedirect('Historical Report', data['get_report']['type_report'],data['get_report']['timestamp'],data['get_report']['report']);
            }
        } catch (error) {
            console.error(error);
          
        }
    } 
    return (
        <div>
            <br></br>
           <div className={styles['title']}>
                <h1>Historical {reports.length > 0 ? formatTitle(reports[0].type_report) : ''}</h1>
            </div>
            <Row>
            <Button onClick={handleGoBack} variant="link">Go Back</Button>
            </Row>
            
            <div className={styles['report-list-container']}>
                {reports.map((report, index) => (
                    <div key={index} className={styles['report-item']}>
                        <h3  ><p className="display-6"><em>{new Date(report.timestamp).toLocaleString()}</em></p></h3>
                        <p>{truncateReport(report.report)}</p>
                    
                        <button onClick={() => handleGetReport(report._id)} className="mt-2 d-none d-md-inline-block">Show more</button>
                        <button onClick={() => handleGetReport(report._id)} className="mt-2 ml-2 d-inline-block d-md-none">Show more</button>

                    </div>
                ))}
         

            </div>

        </div>
    );
};

export default ReportList;
